.skills {
    background-color: #1a1a1a; /* Fondo para diferenciar */
}

/*  Contenedor de Skills */
.skills-container {
    border: solid 1px #b3b3b3a4;
    border-radius: 10px;
    padding: 5px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr)); /* Ajusta el tama帽o autom谩ticamente */
    grid-gap: 10px;
    gap: 10px; /* Asegura espacio entre los elementos */
    justify-content: center;
}

/*  Estilo de cada Skill */
.skill-card {
    background: #222;
    padding: 15px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    text-align: center;
    transition: transform 0.3s ease-in-out;
}

.skill-card:hover {
    transform: scale(1.05);
}

/*  Iconos de Skills */
.skill-icon {
    width: 30px;
    height: 30px;
    object-fit: contain;
    margin-bottom: 10px;
}

.skill-card span {
    font-weight: bold;
    display: block;
    margin-bottom: 5px;
}

.skill-card small {
    color: #ccc;
}

.language-card {
    margin: 30px;
    text-align: left;
}

/*  Para pantallas peque帽as */
@media (max-width: 768px) {
    .skills-container {
        grid-template-columns: repeat(auto-fit, minmax(110px, 1fr));
        width: 100%;
    }
}
/*  Contenedor General */
.experience-education-container {
    display: flex;
    flex-direction: column;
    grid-gap: 40px;
    gap: 40px;
    width: 90%;
    margin: 0 auto;
    text-align: center;
}

.experience-education-container h2 {
    color: crimson;
}

/*  Secciones de Experiencia y Educaci贸n */
.education-section {
    background: #222;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.3);
}

/*  Tarjetas de Experiencia y Educaci贸n */
.experience-card, .education-card {
    background: #333;
    padding: 15px;
    margin: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    transition: transform 0.3s;
}

.experience-card:hover, .education-card:hover {
    transform: scale(1.05);
}

/*  Grid para organizar tarjetas */
.experience-grid, .education-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    grid-gap: 20px;
    gap: 20px;
    margin-top: 20px;
}

/*  Carrusel */
.carousel-container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin: auto;
    padding: 10px 40px;
    position: relative;
    overflow: hidden;
}

.carousel-track {
    display: flex;
    grid-gap: 10px;
    gap: 10px;
    overflow: hidden;
    transition: transform 0.5s ease-in-out;
}

.carousel-button {
    background: #b3b3b3a4;
    color: white;
    border: none;
    padding: 10px 15px;
    cursor: pointer;
    position: absolute;
    z-index: 2;
    font-size: 24px;
    border-radius: 50%;
    transition: background 0.3s ease-in-out;
}

.carousel-button:hover {
    background: rgba(0, 0, 0, 0.7);
}

.carousel-button.left {
    left: 5px;
}

.carousel-button.right {
    right: 5px;
}

/*  Iconos de experiencia organizados en filas */
.experience-card ul.icons-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(40px, 1fr)); /* Se ajusta autom谩ticamente */
    grid-gap: 0.5rem;
    gap: 0.5rem;
    margin-top: 1rem;
    align-items: center;
    justify-items: center;
}

.experience-card ul.icons-container li.icon {
    text-align: center;
    font-size: 1.5rem;
    list-style-type: none;
}

.experience-card ul.icons-container li.icon img {
    height: 2rem;
}

/*  DESCRIPCIN */
.description {
    text-align: left;
}

.description ul {
    margin: 20px;
}

/*  RESPONSIVE DESIGN */

/*  Tablets */
@media (max-width: 1024px) {
    .experience-grid, .education-grid {
        grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    }

    .carousel-container {
        padding: 10px;
    }
}

/*  M贸viles */
@media (max-width: 768px) {
    .experience-grid, .education-grid {
        grid-template-columns: 1fr;
    }

    .carousel-container {
        flex-direction: column;
        align-items: center;
    }

    .carousel-button {
        font-size: 20px;
        padding: 8px 12px;
    }

    .experience-card, .education-card {
        padding: 12px;
        margin: 10px;
    }

    .experience-card ul.icons-container {
        grid-template-columns: repeat(5, 1fr); /*  5 iconos por fila */
    }
}

/*  M贸viles peque帽os */
@media (max-width: 480px) {
    .carousel-button {
        font-size: 18px;
        padding: 6px 10px;
    }

    .experience-card ul.icons-container {
        grid-template-columns: repeat(4, 1fr); /*  4 iconos por fila */
    }

    .experience-card {
        padding: 10px;
        font-size: 14px;
    }

    .education-card {
        padding: 10px;
        font-size: 14px;
    }

    .description {
        font-size: 12px;
    }
}

.certifications-container {
