/*  Contenedor de Insignias */
.badges-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr); /*  3 columnas en pantallas grandes */
    grid-gap: 10px;
    gap: 10px;
    margin-top: 10px;
}

/*  Insignia Individual */
.badge-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 10px;
    border-radius: 5px;
    transition: transform 0.2s ease-in-out;
}

.badge-item:hover {
    transform: scale(1.05);
}

.badge-item img {
    width: 100px;
    height: 100px;
}

.badge-item p {
    margin-top: 5px;
    font-size: 14px;
}

/*  **RESPONSIVE**: En modo horizontal en m贸viles (pantallas anchas pero cortas) */
@media (max-width: 850px) {
    .certification-card {
        width: calc(50% - 15px);
        margin: auto;
    }

    .badges-container {
        grid-template-columns: repeat(2, 1fr); /*  Regresa a 3 columnas en horizontal */
    }

    .badge-item img {
        width: 60px; /*  Vuelve al tama帽o normal */
        height: 60px;
    }

    .badge-item p {
        font-size: 14px; /*  Texto en tama帽o normal */
    }
}

/*  **RESPONSIVE**: Para dispositivos m贸viles (pantallas menores a 768px) */
@media (max-width: 768px) {
    .certifications-grid {
        flex-direction: column; /*  Las tarjetas se apilan en una sola columna */
    }

    .certification-card {
        width: 90%; /*  Ocupa casi todo el ancho en m贸viles */
        margin: auto;
    }

    .badges-container {
        grid-template-columns: 1fr; /*  Solo una insignia por fila en m贸viles */
    }

    .badge-item {
        flex-direction: column; /*  Imagen y texto en una sola columna */
        text-align: center;
    }

    .badge-item img {
        width: 80px; /*  Se reduce el tama帽o de la imagen en m贸viles */
        height: 80px;
    }

    .badge-item p {
        font-size: 12px; /*  Texto m谩s peque帽o en m贸viles */
    }
}

