/* Estilos de las p谩ginas */
.page {
    width: 50%;
    height: 100%;
    padding: 20px;
    box-sizing: border-box;
    position: absolute;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}

/* P谩gina izquierda (Texto y Tecnolog铆as) */
.left-page {
    border-radius: 10px 0 0 10px;
    background: #b3b3b3a4;
    text-align: left;
    color: #111;
    padding: 30px;
    left: 0;
    transform-origin: right center; 
    overflow-y: auto;
    max-height: 100%;
}

/* P谩gina derecha (Imagen o Video) */
.right-page {
    border-radius: 0 10px 10px 0;
    background: white;
    color: #111;
    display: flex;
    align-items: center;
    justify-content: center;
    right: 0;
    transform-origin: left center;
}

/* Animaci贸n para pasar p谩gina a la derecha */
.flip-right-anim {
    animation: flipRight 1s forwards;
}

@keyframes flipRight {
    0% { transform: rotateY(0); }
    50% { transform: rotateY(-90deg); }
    100% { transform: rotateY(-180deg); }
}

/* Animaci贸n para regresar la p谩gina a la izquierda */
.flip-left-anim {
    animation: flipLeft 1s forwards;
}

@keyframes flipLeft {
    0% { transform: rotateY(0); }
    50% { transform: rotateY(90deg); }
    100% { transform: rotateY(180deg); }
}

