    }

    .prev-btn, .next-btn {
        display: none; /* Eliminamos botones en m贸viles */
    }
    
    .project-icons {
        display: flex;
    }

}

/*  M贸viles Peque帽os */
@media (max-width: 480px) {
    .projects-container {
        padding: 30px 10px;
    }

    .left-page, .right-page {
        padding: 10px;
        font-size: 14px;
    }

    .description {
        font-size: 12px;
    }

    .prev-page, .next-page {
        display:flex;
        background: crimson;
        color: white;
        border: none;
        padding: 10px 15px;
        font-size: 24px;
        cursor: pointer;
        border-radius: 50%;
        width: 33.33%;
        justify-content: center;
        align-items: center;
        margin: auto;
    }
    
    .right-page {
        height: 400px;
        margin-top: -10px;
    }

    .project-icons {
        display: none;
    }

    .video {
        margin-bottom: 40px;
    }
}
.home {
    margin-top: 3%;
    padding: 4%;
}
/*  Navbar */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    top: 0;
    width: 100%;
    background-color: #222;
    padding: 15px 20px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
    z-index: 1000;
}

.logo {
    font-size: 60px;
    font-weight: bold;
    color: crimson;
    background-color: transparent;
}

.logo:hover {
    font-size: 70px;
    background-color: transparent;
}

/*  Bot贸n de hamburguesa */
.menu-toggle {
    display: none; /* Oculto en pantallas grandes */
    font-size: 30px;
    background: none;
    border: none;
    color: white;
    cursor: pointer;
}

/*  Men煤 */
.nav-links {
    margin-top: -10px;
    margin-bottom: -3px;
    list-style: none;
    display: flex;
    grid-gap: 20px;
    gap: 20px;
}

.nav-links button {
    background: none;
    border: none;
    color: white;
    font-size: 16px;
    cursor: pointer;
    transition: color 0.3s;
}

.nav-links button.active {
    color: crimson;
    font-weight: bold;
    border-bottom: 2px solid rgb(173, 33, 61);
}

.nav-links button:hover {
    background-color: transparent;
    color: crimson;
    font-size: 25px;
    font-weight: bold;
    transform: scale(1.01);
}

/*  Submen煤 */
.submenu {
    position: relative;
}

.submenu-content {
    display: block;
    position: absolute;
    background-color: white;
    min-width: 250px;
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
    z-index: 1;
}

.submenu-content li {
    display: block;
}

.submenu-content button {
    width: 100%;
    padding: 10px;
    text-align: left;
    border: none;
    background: none;
    cursor: pointer;
    color: #333;
}

.submenu-content button:hover {
    background-color: #f1f1f1;
}

/*  Estilos Responsive */
@media (max-width: 768px) {
    .submenu-content,
    .navbar {
        width: 100vw;
    }
    
    .menu-toggle {
        display: block; /* Mostrar bot贸n de hamburguesa */
    }

    .nav-links {
        display: none; /* Ocultar men煤 en m贸viles */
        flex-direction: column;
        position: absolute;
        top: 80px;
        left: 0;
        width: 100vw;
        background-color: #222;
        padding: 20px 0;
    }

    .nav-links.open {
        display: flex; /* Mostrar cuando se active */
    }

    .nav-links li {
        text-align: center;
        width: 100%;
    }
    
    .logo {
        font-size: 40px;
    }

    .logo:hover {
        font-size: 50px;
    }

}
